.quotes {
  max-width: 90vw;
  width: 700px;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

@media (max-width: 768px) {
  .quotes {
    gap: 2rem;
    margin: 2rem auto;
  }
}

.paragraph {
  text-align: center;
  font-size: 1.125rem;
}

.paragraph a {
  text-decoration: none;
  font-weight: 600;
  color: rgba(164, 42, 42, 1);
}

.sorting {
  min-width: fit-content;
  padding: 0.6rem 2rem;
  width: 20px;
  background-color: transparent;
  border: 2px solid rgba(164, 42, 42, 1);
  color: rgba(164, 42, 42, 1);
  font-weight: 600;
  border-radius: 3px;
  cursor: pointer;
  transition: 250ms ease-out;
}

.sorting:hover {
  color: #fefefe;
  background-color: rgba(164, 42, 42, 1);
}
