.spinner {
  margin: 0 auto;
  text-align: center;
}

.spinner svg {
  animation-name: spinner;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  font-size: 3rem;
  margin: 0 auto;
  fill: rgba(164, 42, 42, 1);
}

@keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
